.about {
    padding-top: 100px;
    width: 100%;
    height: 100%;
    background-color: rgb(250, 227, 240);
    padding-bottom: 230px;
}


.about_title {
    padding-top: 20px;
    margin-bottom: 50px;
    font-size: 50px;
    font-weight: 700;
    text-align: center;
    color: rgb(241, 102, 114);
}

.about_title1 {
    margin: 30px;
    font-size: 28px;
    font-weight: 700;
    text-align: center;
}

.about_font {
    font-size: 15px;
    text-align: center;
    color: rgb(114, 112, 112);
    margin-bottom: 10px;
}

.about_font1 {
    margin-top: 10px;
    font-size: 18px;
    font-weight: 700;
    text-align: center;
    /* color: rgb(122, 105, 221); */
}

.about_font2 {
    font-size: 20px;
    font-weight: 600;
    text-align: center;
    /* color: rgb(122, 105, 221); */
    margin-bottom: 10px;
    margin-top: 10px;
}

.about_font3 {
    font-size: 18px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 50px;
}

.about_font4 {
    font-size: 17px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 10px;
}

.about_font4_1 {
    font-size: 17px;
    font-weight: 500;
    text-align: center;
    margin-bottom: 50px;
}

.about_font5 {
    font-size: 16px;
    font-weight: 800;
    text-align: center;
}

.about_paper1 {
    border-radius: 100px;
    height: 100px;
    width: 100px;
    text-align: "center";
    padding: 20px;
    margin: auto;
    background-color: white;
  }


  @media (max-width: 900px) {
    .about_title {
        padding-top: 20px;
        margin-bottom: 40px;
        font-size: 45px;
        font-weight: 700;
        text-align: center;
        color: rgb(241, 102, 114);
    }

    .about_title1 {
        margin: 20px;
        font-size: 25px;
        font-weight: 700;
        text-align: center;
    }
    
    .about_font {
        font-size: 14px;
        text-align: center;
        color: rgb(114, 112, 112);
        margin-bottom: 8px;
    }
    
    .about_font1 {
        padding: 20px;
        padding-top: 0px;
        padding-bottom: 0px;
        font-size: 15px;
        font-weight: 700;
        text-align: center;
        /* color: rgb(122, 105, 221); */
    }
    
    .about_font2 {
        font-size: 20px;
        font-weight: 600;
        text-align: center;
        /* color: rgb(122, 105, 221); */
        margin-bottom: 10px;
    }
    
    .about_font3 {
        font-size: 16px;
        font-weight: 500;
        text-align: center;
        margin-bottom: 50px;
    }
    
    .about_font4 {
        font-size: 17px;
        font-weight: 500;
        text-align: center;
        margin-bottom: 10px;
    }

    .about_font4_1 {
        font-size: 17px;
        font-weight: 500;
        text-align: center;
        margin-bottom: 55px;
    }
    
    .about_font5 {
        font-size: 16px;
        font-weight: 800;
        text-align: center;
    }
    
    .about_paper1 {
        border-radius: 100px;
        height: 100px;
        width: 100px;
        text-align: "center";
        padding: 20px;
        margin: auto;
        background-color: white;
      }
}